@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;600&display=swap");

/* FONT SETUP */
body {
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  color: #e0f7fa;
}

h1,
h2,
h3,
h4,
h5,
h6,
.navbar a,
.footer h1,
header h1 {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  color: #66fcf1;
}

a {
  color: #4ee5e5;
  text-decoration: none;
  transition: 0.3s;
}

a:hover {
  color: #00f0ff;
  text-shadow: 0 0 6px #00f0ff;
}

/* Kalo mau tombol pakai Raleway juga */
button,
.btn {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
}

html {
  height: 100%;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrapper {
  flex: 1;
}

body {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  position: relative;

  /* Gabungkan dua lapisan gradien */
  background-image: linear-gradient(
      to bottom,
      #05161a,
      #072e33,
      #0c7076,
      #0f908c,
      #6da5c0,
      #294d61
    ),
    repeating-linear-gradient(
      45deg,
      rgba(255, 255, 255, 0.008) 0px,
      rgba(0, 0, 0, 0.008) 2px
    );

  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: overlay;

  color: #e0f7fa; /* warna teks utama */
}

/* Navbar styling */
.navbar {
  background: linear-gradient(
    to top,
    #0f2027,
    #203a43,
    #2c5364
  ); /* Sesuai warna OnlyJack */
  color: white;
  padding: 10px 20px;
  font-family: "Poppins", sans-serif;
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

/* hamburger */
/* Sembunyikan hamburger di layar besar */
.hamburger {
  display: none;
  font-size: 24px;
  cursor: pointer;
  color: white;
  margin-right: 15px;
}

/* Tampilkan hamburger dan sembunyikan nav-links saat di layar kecil */
@media screen and (max-width: 768px) {
  .hamburger {
    display: inline-block;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: rgba(15, 32, 39, 0.98);
    padding: 10px 20px;
    z-index: 998;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-container {
    justify-content: space-between;
    align-items: center;
  }
}

.nav-links {
  transition: all 0.3s ease-in-out;
}

/* Link Navigasi */
.nav-links {
  display: flex;
  gap: 20px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  position: relative;
  transition: all 0.3s ease;
}

.nav-links a.current::after,
.nav-links a:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #00ffff;
  bottom: -4px;
  left: 0;
}

.nav-links a:hover {
  color: #00ffff;
}

/* Ikon */
.nav-icons {
  display: flex;
  gap: 15px;
  font-size: 18px;
  cursor: pointer;
}

.nav-icons i {
  color: white;
  transition: color 0.3s ease;
}

.nav-icons i:hover {
  color: #00ffff;
}

.nav-icons {
  display: flex;
  align-items: center;
  gap: 15px;
}

// language switcher
.language-switcher {
  margin-left: 10px;
}

.language-switcher select {
  background: transparent;
  border: 1px solid #00ffff;
  color: white;
  padding: 4px 6px;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
}

.language-switcher select option {
  background-color: #0f172a;
  color: white;
}

#theme-toggle {
  font-size: 20px;
  cursor: pointer;
  transition: color 0.3s ease;
  color: #ffffff;
}

#theme-toggle:hover {
  color: #00ffff;
}

/* Responsive */
@media screen and (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .nav-icons i.fa-bars {
    display: inline-block;
  }
}

/* Footer styling */
.footer {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 14px;
  background-color: rgba(7, 46, 51, 0.9);
  backdrop-filter: blur(10px);
  border-top: 1px solid transparent;
}

.footer p {
  color: #e0f7fa;
}

.footer p i {
  color: #7ce7f8;
  transition: all 0.3s;
}

.footer p i:hover {
  transform: scale(1.1);
}

.footer span {
  font-weight: 700;
  color: #6de0f7;
}

.footer .social-icon {
  text-decoration: none;
  font-size: 1em;
  color: #e0f7fa;
  transition: transform 0.3s, color 0.3s;
}

.footer .social-icon i:hover {
  transform: scale(1.1);
  color: #a6f1e0;
}

// Home Styling

/* Container styling */
.container,
.friends-container,
.blog-container {
  max-width: 1400px;
  margin: 60px auto 40px;
  padding: 0 24px;
  box-sizing: border-box;
}

.container h1,
.friends-container h1,
.blog-container h1 {
  font-size: 2rem;
  color: #6de0f7;
  margin-bottom: 10px;
}

.container h2,
.blog-container h2,
.blog-container h3 {
  color: #7ce7f8;
}

.container .last_name {
  color: #a6f1e0;
}

a {
  color: #82f5ff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: #a6f1e0;
}

/* List + Glassmorphism */
.hobbies,
.friend-list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 12px;
}

.hobbies li,
.friend-list li,
.blog-container > *:not(h1) {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px);
  border-radius: 12px;
  padding: 12px 16px;
  box-shadow: 0 0 10px rgba(173, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.3s, box-shadow 0.3s;
}

.hobbies li:hover,
.friend-list li:hover,
.blog-container > *:not(h1):hover {
  transform: translateY(-4px);
  box-shadow: 0 0 20px rgba(173, 255, 255, 0.5);
}

.friend-list a {
  color: #c6f7ff;
  font-weight: 500;
}

.friend-list a:hover {
  color: #a6f1e0;
}

/* Blog specifics */
.blog-container p {
  line-height: 1.7;
  margin-bottom: 1em;
}

.blog-container ul,
.blog-container ol {
  margin-left: 1.5em;
  margin-bottom: 1em;
}

.blog-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 15px;
  padding: 0;
  list-style: none;
  margin: 0;
}

.blog-item {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(12px);
  border-radius: 12px;
  padding: 16px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 12px rgba(0, 255, 255, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  color: #e0faff;
}

.blog-item:hover {
  transform: translateY(-6px);
  box-shadow: 0 0 25px rgba(0, 255, 255, 0.4);
}

.blog-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 6px;
}

.blog-icon {
  font-size: 1.2rem;
}

.blog-item a {
  color: #7fdfff;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.2s ease;
}

.blog-item a:hover {
  color: #a0f4ff;
  text-decoration: underline;
}

.blog-date {
  display: inline-block;
  background: rgba(0, 255, 255, 0.15);
  color: #c8f7ff;
  font-size: 0.85em;
  padding: 3px 8px;
  border-radius: 20px;
  margin-bottom: 10px;
}

.blog-excerpt {
  font-size: 0.95em;
  color: #c4f6ff;
  margin-top: 8px;
  line-height: 1.5;
}

/* Efek kaca hanya untuk daftar blog */
.blog-list .blog-item {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(6px);
  transition: transform 0.2s, box-shadow 0.2s;
}

.blog-list .blog-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.3);
}

/* Header dan date styling */
.blog-header {
  font-size: 1.2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.blog-date {
  font-size: 0.9rem;
  color: #aaa;
  display: block;
  margin-top: 0.5rem;
}

/* Supaya tidak semua elemen di dalam blog-container terkena efek kotak */
.blog-container > *:not(ul):not(h1):not(h2) {
  background: transparent !important;
  box-shadow: none !important;
  padding: 0 !important;
  border: none !important;
}

// blog-search
/* --- Search Box Styling --- */
.search-container {
  text-align: center;
  margin: 20px 0;
}

.search-input {
  padding: 10px 16px;
  width: 60%;
  max-width: 500px;
  border-radius: 10px;
  border: 1px solid var(--accent-color);
  background-color: rgba(255, 255, 255, 0.08);
  color: white;
  font-size: 16px;
  transition: 0.3s ease;
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.search-input:focus {
  outline: none;
  border-color: var(--accent-color);
  background-color: rgba(255, 255, 255, 0.12);
}

.search-button {
  padding: 10px 24px;
  margin-left: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(6px);
  color: var(--button-text-color, white);
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
}

.search-button:hover {
  background: var(--accent-color);
  color: #111;
  transform: translateY(-2px);
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.35);
}

/* Ocean mode (default): white text di atas tombol ocean */
:root {
  --button-text-color: white;
}

/* Volcano mode: pakai teks gelap di atas warna terang */
body.volcano-mode {
  --button-text-color: #222;
}

body.volcano-mode .search-input {
  background-color: rgba(255, 192, 160, 0.08);
  color: #222;
}

body.volcano-mode .search-input::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

body.volcano-mode .search-input:focus {
  background-color: rgba(255, 192, 160, 0.15);
  border-color: var(--accent-color);
}

body.volcano-mode .search-button {
  color: #222;
}

/* Responsive */
@media (max-width: 768px) {
  .nav-links {
    flex-direction: column;
    gap: 10px;
    background-color: rgba(7, 46, 51, 0.9);
    padding-top: 10px;
  }

  .container,
  .friends-container,
  .blog-container {
    padding: 0 16px;
  }
}

