  body.volcano-mode .nav-links {
    background-color: rgba(54, 20, 38, 0.9);
  }
}

///* ========== icon click ============== */

// clockbar menu
/* Sidebar overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 100vh;
  overflow: hidden;
  background: rgba(15, 15, 30, 0.95);
  backdrop-filter: blur(10px);
  z-index: 9999;
  transition: width 0.3s ease;
}

.sidebar-overlay.active {
  width: 300px;
}

.sidebar-content {
  width: 300px;
  height: 100%;
  padding: 20px;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 24px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.datetime {
  text-align: center;
  font-size: 20px;
}

#clock {
  font-size: 36px;
  margin-bottom: 10px;
  font-weight: bold;
}

/* === Sticky Footer Layout === */
html,
body {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

body > *:not(.sidebar-overlay) {
  flex-shrink: 0;
}

body > .content-wrapper {
  flex: 1 0 auto;
}

footer {
  flex-shrink: 0;
}
